import { TYPES } from 'src/components/data'
import Input from '@material-ui/core/Input';
import Slider from '@material-ui/core/Slider';
import Select from '@material-ui/core/Select';

const getCalculateNewValues = (key, value, values) => {
    const newValues = Object.assign({}, values, { [key]: value });
    // All if's here
    if (tableValues.d10 && tableValues.d11) { // d12
        tableValues['d12'] = tableValues.d10 - tableValues.d11
    }
    if (tableValues.d21 && tableValues.d22) { // d23
        tableValues['d23'] = tableValues.d21 - tableValues.d22
    }
    if (tableValues.e30 && tableValues.d32) { // e32
        tableValues['e32'] = tableValues.e30 * tableValues.d32
    }
    if (tableValues.e30 && tableValues.d32) { // f32
        tableValues['f32'] = tableValues.e30 * tableValues.d32
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d34) { // f34
        tableValues['f34'] = tableValues.f32 / tableValues.d32 * tableValues.d34
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d35) { // f35
        tableValues['f35'] = tableValues.f32 / tableValues.d32 * tableValues.d35
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d36) { // f36
        tableValues['f36'] = tableValues.f32 / tableValues.d32 * tableValues.d36
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d37) { // f37
        tableValues['f37'] = tableValues.f32 / tableValues.d32 * tableValues.d37
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d38) { // f38
        tableValues['f38'] = tableValues.f32 / tableValues.d32 * tableValues.d38
    }
    if (tableValues.f32 && tableValues.d32 && tableValues.d39) { // f39
        tableValues['f39'] = tableValues.f32 / tableValues.d32 * tableValues.d39
    }
    if (tableValues.e30) { // d47
        tableValues['d47'] = tableValues.e30
    }
    if (tableValues.d49) { // d48
        tableValues['d48'] = tableValues.d49 / 560 / 1000
    }
    if (tableValues.e49) { // e48
        tableValues['e48'] = tableValues.e49 / 560 / 1000
    }
    if (tableValues.d11 && tableValues.d23) { // d49 AND e49
        tableValues['e49'] = tableValues.d11 * tableValues.d23 * 1000
    }
    if (tableValues.d49) { // d50 
        tableValues['d50'] = tableValues.d49 * 0.000330693393472
    }
    if (tableValues.e49) { // e50
        tableValues['e50'] = tableValues.e49 * 0.000330693393472
    }
    if (tableValues.d48 && tableValues.d52) { // d51
        tableValues['d51'] = tableValues.d48 + tableValues.d52
    }
    if (tableValues.e48 && tableValues.e52) { // e51
        tableValues['e51'] = tableValues.e48 + tableValues.e52
    }
    if (tableValues.d48 && tableValues.d47) { // d52
        tableValues['d52'] = tableValues.d48 * (1 / tableValues.d47)
    }
    if (tableValues.e48 && tableValues.e47) { // e52
        tableValues['e52'] = tableValues.e48 * (1 / tableValues.e47)
    }
    // end
    return newValues
}

export const handleInputChange = (event, updateTablesValues) => {
    const values = {}; // All data here
    const field = event.target.id;
    const newValue = event.target.value;
    const newValues = getCalculateNewValues(field, newValue, values)
    updateTablesValues({ values: newValues })
};

const handleSelectChange = (event, updateTablesValues) => {
    updateTablesValues({ values: { [event.target.id]: event.target.value } })
};

const onChangeSlider = (event, val, location, updateTablesValues) => {
    updateTablesValues({ values: { [location]: val } })
};


function valuetext(value) {
    return `${value}`;
}

const getMarks = (row, ) => {
    if (!row) return [];
    let arr = []
    let i = row.min
    while (i <= row.max) {
        arr.push({ value: i })
        i += row.ticks
    }
    // if we want lables in the  edges of the slider
    // if (arr.length) { 
    //     debugger
    //     arr[0].label = row.min.toString()
    //     arr[arr.length - 1].label = row.max.toString()
    // }
    return arr
}

export const renderValueType = (row, updateTablesValues, tableValues, classes) => {
    const type = row.type
    switch (type) {
        case TYPES.NUMERIC:
            return <Input
                id={row.location}
                label="Number"
                value={tableValues[row.location] || ''}
                onChange={(e) => handleInputChange(e, updateTablesValues)}
                type="number"
                className={classes.textField}

            />
        case TYPES.RANGE:
            return <Slider
                defaultValue={Math.floor((row.min + row.max) / 2)}
                getAriaValueText={valuetext}
                onChange={(e, val) => onChangeSlider(e, val, row.location, updateTablesValues)}
                aria-labelledby="discrete-slider-always"
                step={row.ticks}
                min={row.min}
                max={row.max}
                marks={getMarks(row)}
                valueLabelDisplay="on"
            />
        case TYPES.SELECT:
            return <Select
                id={row.location}
                native
                value={tableValues[row.location]}
                onChange={(e) => handleSelectChange(e, updateTablesValues)}
            >
                {row.data.map((item, i) => {
                    return <option key={i} value={item}>{item}</option>
                })}
            </Select>
        case TYPES.NOT_EDITABLE:
            return <Input
                id={row.location}
                value={tableValues[row.location] || ''}
                readOnly
                className={classes.textFieldUNEditable}
            />
        default:
            return row.type
    }
}